<template>
  <div class="saleList">
     
    <div class="adWrapper">
      <advertisement></advertisement>
    </div>
    
    
    <ul>
      <li v-for="item in goodList" class="goodItem">
          <goodCard v-bind:good="item"></goodCard>
      </li>
    </ul>
    
    <laypage :pages="pages"></laypage>
  </div>
</template>

<script>
import { bus } from '../../assets/bus.js';
import goodCard from '../goodCard/goodCard.vue';
import laypage from '../laypage/laypage.vue';
import advertisement from '../advertisement/advertisement.vue';
export default {
  data(){
    return{
      goodListUrl:"/dachuang/sale",
      goodList:[],
      pages:""
    }
  },
  created(){
    var vm=this;
    vm.getGoodList(0);
    bus.$on("isGetGoodList",function(type){

      vm.getGoodList(type);
    })
  },
  methods:{
    getGoodList:function(type){
      var url=this.goodListUrl+"?type="+type+"&page="+1;
     // var url=this.goodListUrl+type+".json";
      this.$http.get(url).then(function(res){
        this.goodList=res.body.data;
        //this.pages=res.body.pages;
        console.log(red.body);
      },function(err){
        console.log(err);
      })
      
    }
  },
  components:{
    "goodCard":goodCard,
    "laypage":laypage,
    "advertisement":advertisement
  }
}
</script>

<style scoped>
  .adWrapper{
    margin: 20px 0;
  }
  .saleList{
    width: 95%;
    display: inline-block;
    padding-left: 0%;
    text-align:left;
  }
  .saleList li{
     display: inline-block;
     margin: 20px;
     list-style: none;
  }
  .saleList ul{
    padding: 20px 20px 80px 20px;
  }
</style>


