<template>
  <div class="goodCard" @click="">
    <a>
    <div class="layer"></div>
    <div class="imgWrapper">
      <img @load="loadImg" :src="this.good.img" :class="{imgLoaded:loaded}"></img>
    </div>
    <div class="inforWrapper">
      <h2>{{this.good.name}}</h2>
      <span class="price">${{this.good.price}}</span>
      <span class="clickTimes">点击数{{this.good.clickTimes}}</span>
    </div>
    </a>
  </div>
</template>

<script>

export default {
  data(){
    return{
      goodUrl:"http://localhost:3000",
      information:{

      },
      loaded:false
    }
  },
  
  methods:{
    loadImg:function(){
      this.loaded=true;
    }
  },
  props:["good"]
}
</script>

<style scoped>
  .goodCard a{
    width: 200px;
    height: 300px;
    display: inline-block;
  }
  .goodCard{
    width: 200px;
    height: 300px;
    display: inline-block;
    padding: 8px 8px;
    position: relative;
    background:#fff;
    border: 1px solid rgba(0,0,0,0.1);
    box-shadow: 2px 2px 20px rgba(0,0,0,0.2);
  }
  .imgWrapper{
    width: 200px;
    height: 200px;
    display: inline-block;
    background: url(loading1.gif) no-repeat;
    background-size: 100%;

  }
  .imgLoaded{
    display: block;
    opacity: 1;
    animation:myfirst 2s;
  }
  @keyframes myfirst
  {
  from {opacity:0;}
  to {opacity: 1;}
  }
  img{
    display: none;
    opacity: 0;
    width: 200px;
    height: 200px;   
  }
  .inforWrapper{
    height: 100px;
    width: 200px;
    display: inline-block;
  }
  .layer{
    width: 185px;
    height: 300px;
    position: absolute;
    left: 5px;
    top: 10px;
    display: inline-block;
    padding: 8px 8px;
    z-index: 0;
    border: 1px solid rgba(0,0,0,0.1);
    box-shadow: 2px 2px 10px rgba(0,0,0,0.2);
  }
  .goodCard:hover{
    top:-20px;
    box-shadow-bottom: 2px 2px 5px rgba(0,0,0,0.3);
    cursor: pointer;
    transition:1s all;
  }
  .layer:hover{
    top: 5px;
    box-shadow: 2px 2px 10px rgba(0,0,0,0.5);
    cursor: pointer;
    transition:0.6s ease-out;
    transition-delay:0.2s;
  }
  .price{
    width: 190px;
    height: 18px;
    line-height: 18px;
    display: block;
    text-align: right;
    color: red;
    font-size: 22px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  a h2{
    height: 18px;
    display: inline-block;
    width: 150px;
    line-height: 18px;
    margin: 10px 0 10px 10px;
    text-align: left;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  .clickTimes{
    padding: 10px;
    display: block;
    text-align: right;
    color:rgba(0,0,0,0.5);
  }
  
</style>


